import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IServiceCat} from '../../../../../../model/main/hotel/service';

@Component({
  selector: 'app-advanced-filter',
  templateUrl: './advanced-filter.component.html',
  styleUrls: ['./advanced-filter.component.sass']
})
export class AdvancedFilterComponent implements OnInit {

  listItemChange = [];
  priceFilter = 20;
  @Input() filterItems: IServiceCat[];
  @Output() itemFilterChange = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  onValueChange(item) {
    if (item.value) {
      this.listItemChange.push(item);
    } else {
      this.listItemChange = this.listItemChange.filter(inListItem => inListItem.name !== item.name);
    }
    this.itemFilterChange.emit(this.listItemChange);
  }

  onPriceChange(price) {
    console.log(price);
  }

}



