import { Injectable } from '@angular/core';
import {ISearchRequest} from '../../../model/on-app/search-data';
import {IHotel} from '../../../model/main/hotel/hotel';
import {MockHotel, MockRoomType} from '../../mock-data/hotel';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {IRoomType} from "../../../model/main/hotel/room";

@Injectable()
export class HotelService {

  private $hotelList: BehaviorSubject<IHotel[]> = new BehaviorSubject(MockHotel);
  private $roomTypes: BehaviorSubject<IRoomType[]> = new BehaviorSubject(MockRoomType);

  constructor() { }

  readHotelByOption(searchRequest: ISearchRequest): Promise<IHotel[]> {
    if (searchRequest.place) {
      return Promise.resolve(this.$hotelList.getValue().filter(hotel => hotel.placeID === searchRequest.place));
    }
  }

  readHotelByID(hotelID: string): Promise<IHotel> {
    return Promise.resolve(this.$hotelList.getValue().find(hotel => hotel.hotelID === hotelID));
  }

  readRoomByHotelID(hotelID: string): Promise<IRoomType[]> {
    return Promise.resolve(this.$roomTypes.getValue().filter(roomType => roomType.hotelID === hotelID));
  }

}



