import { Component, OnInit } from '@angular/core';
import {HotelService} from '../../../../service/hotel/hotel.service';
import {ActivatedRoute, Router} from "@angular/router";
import {IHotel} from "../../../../../model/main/hotel/hotel";
import {IRoomType} from "../../../../../model/main/hotel/room";
import {IOrderDetail} from "../../../../../model/main/order/order";

@Component({
  selector: 'app-hotel-detail',
  templateUrl: './hotel-detail.component.html',
  styleUrls: ['./hotel-detail.component.sass']
})
export class HotelDetailComponent implements OnInit {

  private hotelID: string;
  selectedHotel: IHotel;
  roomTypeList: IRoomType[];
  selectedRoomType: IRoomType;
  private orderDetail: IOrderDetail = {};
  private routeParams: any;

  constructor(private _hotel: HotelService,
              private _router: Router,
              private _activateRoute: ActivatedRoute) { }

  ngOnInit() {
    this.hotelID = this._activateRoute.snapshot.params['id'];
    this.initData();
    this._activateRoute.queryParams.subscribe(params => this.routeParams = params);
  }

  initData() {
    this._hotel.readHotelByID(this.hotelID).then(hotel => {
      this.selectedHotel = hotel;
      this._hotel.readRoomByHotelID(this.hotelID).then(roomTypes => {
        this.roomTypeList = roomTypes;
        this.selectedRoomType = this.roomTypeList[0];
        this.roomTypeList[0].klass = 'active';
      });
    });
  }

  onRoomTypeChange(roomTypeID) {
    this.selectedRoomType = {...this.roomTypeList.find(type => type.roomTypeID === roomTypeID)};
  }

  onBookNowBtnClick(event) {
    this.orderDetail = event;
    this.orderDetail.hotelName = this.selectedHotel.name;
    this.orderDetail.hotelAddress = this.selectedHotel.address;
    this.orderDetail.room = this.selectedRoomType;
    this.orderDetail.dateIn = this.routeParams.checkin;
    this.orderDetail.dateOut = this.routeParams.checkout;
    this._router.navigate(['/checkout', [JSON.stringify(this.orderDetail)]]);
  }

}



