import { Component, OnInit } from '@angular/core';
import {MockServiceDefault} from '../../../../mock-data/advanced-option';
import {IServiceCat} from '../../../../../model/main/hotel/service';
import {ActivatedRoute, Router} from '@angular/router';
import {ISearchRequest} from "../../../../../model/on-app/search-data";
import {HotelService} from "../../../../service/hotel/hotel.service";
import {IHotel} from "../../../../../model/main/hotel/hotel";

@Component({
  selector: 'app-hotel-list',
  templateUrl: './hotel-list.component.html',
  styleUrls: ['./hotel-list.component.sass']
})
export class HotelListComponent implements OnInit {

  advanceFilterList: IServiceCat[] = MockServiceDefault;
  searchRequest: ISearchRequest;
  hotelList: IHotel[];
  immutableHotels: IHotel[];

  constructor(private _router: Router,
              private _activeRoute: ActivatedRoute,
              private _hotel: HotelService) { }

  ngOnInit() {
    if (this._activeRoute.snapshot.params['0']) {
      this.searchRequest = JSON.parse(this._activeRoute.snapshot.params['0']);
      this._hotel.readHotelByOption(this.searchRequest).then(hotels => {
        this.hotelList = hotels;
        this.immutableHotels = hotels;
      });
    }
  }

  /**@Description:
   * Filter hotels that include the selected services
   * @param service item that is changed
   */
  onItemValueChange(event) {
    /**If event have value, so filter the hotel, if not return immutableHotels*/
    if (event.length > 0) {
      let checkList: IHotel[] = [];
      let outputList: IHotel[] = [];
      let isFirstRound = true;

      event.forEach(selected => {
        /**Check if first loop, so filter via immutable list, if not first loop use "filtered first loop list" to filter*/
        if (isFirstRound) {
          this.immutableHotels.forEach(hotel =>
            hotel.service.forEach(service =>
              service.serviceItem.forEach(item => {
                if (selected.name === item.name && item.value) {
                  outputList.push(hotel);
                }
              })
            )
          );
          checkList = outputList;
          isFirstRound = false;
        } else {
          if (checkList.length > 0) {
            outputList = [];
            checkList.forEach(hotel =>
              hotel.service.forEach(service =>
                service.serviceItem.forEach(item => {
                  if (selected.name === item.name && item.value) {
                    outputList.push(hotel);
                  }
                })
              )
            );
            checkList = outputList;
          }
        }
      });
      this.hotelList = [...outputList];
    } else {
      this.hotelList = this.immutableHotels;
    }
  }

  onViewHotelBtnClick(hotelId) {
    const routeData = {
      checkin: this.searchRequest.checkin,
      checkout: this.searchRequest.checkout,
    };
    this._router.navigate(['/hotel/detail/', hotelId], {queryParams: routeData});
  }
}



