import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IRoomType} from '../../../../../../model/main/hotel/room';
import {Router} from '@angular/router';
import {IOrderDetail} from '../../../../../../model/main/order/order';

@Component({
  selector: 'app-room-info',
  templateUrl: './room-info.component.html',
  styleUrls: ['./room-info.component.sass']
})
export class RoomInfoComponent implements OnInit {

  roomQuanOptions: any[] = [];
  private orderDetail: IOrderDetail = {};
  isSelectedQuan = false;
  @Input() roomTypeInfo: IRoomType;
  @Output() handleRoomAccept = new EventEmitter();

  constructor() { }

  ngOnInit() {
    this.transformRoomSlotToQuan();
  }

  transformRoomSlotToQuan() {
    for (let i = 1; i <= 5; i++) {
      this.roomQuanOptions.push({
        value: i,
        label: i
      });
    }
  }

  onRoomQuanChange(event) {
    this.orderDetail.quantity = event.value;
    this.isSelectedQuan = true;
  }

  onBookingBtnClick() {
    if (this.orderDetail.quantity !== undefined) {
      this.handleRoomAccept.emit(this.orderDetail);
      this.isSelectedQuan = true;
    } else {
      this.isSelectedQuan = false;
    }
  }

}



