import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HotelListComponent } from './hotel-list/hotel-list.component';
import { HotelDetailComponent } from './hotel-detail/hotel-detail.component';
import { HotelComponent } from './hotel.component';
import {HotelRoutingModule} from './hotel-routing.module';
import { AdvancedFilterComponent } from './hotel-list/advanced-filter/advanced-filter.component';
import { InnerNavSearchComponent } from './inner-nav-search/inner-nav-search.component';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {
  MatDatepickerModule, MatInputModule, MatNativeDateModule, MatSelectModule,
  MatSliderModule
} from '@angular/material';
import {FormsModule} from '@angular/forms';
import {HotelService} from '../../../service/hotel/hotel.service';
import {SharingModule} from '../shared/shared.module';
import { RoomInfoComponent } from './hotel-detail/room-info/room-info.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,

    SharingModule,

    HotelRoutingModule,

    PerfectScrollbarModule,

    MatDatepickerModule,
    MatNativeDateModule,
    MatInputModule,
    MatSliderModule,
    MatSelectModule
  ],
  declarations: [
    HotelListComponent,
    HotelDetailComponent,
    HotelComponent,
    AdvancedFilterComponent,
    InnerNavSearchComponent,
    RoomInfoComponent,
  ],
  providers: [
    HotelService
  ]
})
export class HotelModule { }



